import React from "react";
import { Outlet } from "react-router-dom";
import {
  IconAtom,
  IconBntuBuilding,
  IconDocument,
  IconH,
  IconPrinter,
  IconQuotes,
  IconUserEdit,
} from "../../assets/icons/icons";
import Layout from "../../components/layout/Layout";
import MenuTree from "../../components/MenuTree/MenuTree";
import Title from "../../components/Title/Title";

function DataSearch() {
  const menuConfig = [
    { name: "", icon: <IconDocument />, link: "/publications" },
    { name: "", icon: <IconUserEdit />, link: "/authors" },
    { name: "", icon: <IconBntuBuilding />, link: "/organizations" },
    { name: "", icon: <IconQuotes />, link: "/citations" },
    {
      name: " ",
      icon: <IconAtom />,
      link: "/scientific-direction",
    },
    { name: " h", icon: <IconH />, link: "/index-h" },
    {
      name: " ",
      icon: <IconPrinter />,
      link: "/publication-source",
    },
  ];

  return (
    <Layout>
      <div>
        <Title> </Title>
        <MenuTree groupName="search" config={menuConfig} />
      </div>
      <Outlet/>
    </Layout>
  );
}

export default DataSearch;
